<template>
    <div class="turn-page">
        <ul>
            <li v-if="nowPage != 1" @click="turnPage(-1)">上一页</li>
            <li v-if="nowPage > 3" @click="turnPage(1)">1</li>
            <li v-if="nowPage > 4">...</li>
            <li v-for="i in 5" :key="i" :class="{'cur-page': i == 3}"
                v-if="nowPage + i - 3 > 0 && nowPage + i - 3 <= totalPage" 
                @click="turnPage(nowPage + i - 3)">
                {{nowPage + i - 3}}
            </li>
            <li v-if="totalPage-nowPage >= 4">...</li>
            <li v-if="totalPage-nowPage >= 3"  @click="turnPage(totalPage)">{{totalPage}}</li>
            <li v-if="nowPage != totalPage" @click="turnPage(0)">下一页</li>
        </ul>
    </div>
</template>


<script>
export default {
    props:['totalPage','curPage'],
    data(){
        return{
            nowPage:this.curPage,
        }
    },
    methods:{
         turnPage(pageNum){
            if(pageNum == -1){
                if(this.nowPage > 1){
                    this.nowPage --
                }   
            }else if (pageNum == 0){
                if(this.nowPage < this.totalPage){
                    this.nowPage ++
                }
            }else{
                this.nowPage = pageNum
            }
            this.$emit('turn-page',this.nowPage);
        },
    }

}
</script>


<style lang="scss">
    @import "@/assets/css/index/turn-page.scss"
</style>