<template>
    <div class="add-comment" id="send">
        <h3>发表评论</h3>
        <div class="name-email">
            <input type="text" placeholder="昵称" class="user-name" v-model="userName">
            <input type="text" placeholder="邮箱(评论被回复时你能收到通知)" class="user-name email" v-model="email">
        </div>
        <div class="send-content">
            <textarea v-model="comments"></textarea>
        </div>
        <div class="verify">
            <input type="text" v-model="verify">
            <span v-html="code" @click="changeCode">验证码</span>
        </div>
        <div class="submit">
            <input type="submit" value="提交留言" @click="addComment">
            <input type="reset" value="重写">
        </div>
    </div>
</template>


<script>
export default {
    props:['bid','parent','parentName'],
    data(){
        return {
            userName:'',
            email:'',
            comments:'',
            verify:'',
            code:'',
            text:'',
        }
    },
    methods:{
        addComment(){
            if(this.userName == ''){
                alert('昵称不能为空');
            }else if(this.email == ''){
                alert('邮箱不能为空');
            }else if(this.comments == ''){
                alert('评论内容不能为空');
            }else if(this.verify !== this.text){
                alert('验证码错误');
                this.initCode();
            }else{
                this.$axios.get('/insertComments',{
                    params:{
                        bid:this.bid,
                        parent:this.parent,
                        userName:this.userName,
                        comments:this.comments,
                        email:this.email,
                        parentName:this.parentName
                    }
                }).then(res =>{
                    if(res.data.status == 'success'){
                        this.userName = '';
                        this.email = '';
                        this.comments = '';
                        this.verify ='';
                        this.$emit('reload');
                    }
                }).catch(err => console.log(err))
            }   
        },
        initCode(){
            this.$axios.get('/queryRandomCode',{})
                .then(res =>{
                    this.code = res.data.data.data;
                    this.text = res.data.data.text;
                    console.log(this.text);
                }).catch(err => console.log(err))
        },
        changeCode(){
            this.initCode();
        }
    },
    mounted(){
        this.initCode();

    },

}
</script>


<style lang="scss">
    @import "@/assets/css/about/add-comment.scss"
</style>