const randomStr = n =>{
    const strArr = 'abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ1234567890'.split('');
    let str = '';
    for(let i = 0; i < n; i++){
        let index = Math.floor(Math.random()*strArr.length);
        str += strArr[index];
    }
    return str;
}

export { randomStr }