<template>
    <div class="comment-container">
        <h3>关于：目前有<span>{{total}}</span>条留言</h3>
        <div class="comment" v-for="item in message" :key="item.id">
            <div class="comment-title">
                <span class="user">{{item.user_name}}</span>
                <span class="user" v-if="item.parent_name">回复{{item.parent_name}}</span>
                ：发表于 
                <span class="ctime">{{item.ctime}}</span>
                <span class="replay" @click="replayComments(item.blog_id,item.user_name)"> [回复]</span>
            </div>
            <p>{{item.comments}}</p>
        </div>
       
    </div> 
</template>


<script>
import { getDate } from '@/util/getDate.js'
export default {
    props:['bid','randNum','nowPage'],
    data(){
        return{
            total:0,
            message:[],
            pageSize:2,
            curPage:this.nowPage,
        }
    },
    mounted(){
        this.getComments();
        this.getCommentsCount();
    },
    methods:{
        getComments(){
            this.$axios.get('/queryComments',{
                params:{
                    bid:this.bid,
                    page:this.pageSize,
                    curPage:this.curPage
                }
            }).then(res =>{
                const result = res.data.data
                for(let i = 0; i < result.length;i++){
                    result[i].ctime = getDate(result[i].ctime);
                }
                this.message = result;
            }).catch(err => console.loge(err))
        },
        getCommentsCount(){
            this.$axios.get('/queryCommentsCountByBlogId',{
                params:{
                    bid:this.bid
                }
            }).then(res =>{
                this.total = res.data.data[0].count;
                this.$emit('totalCount',this.total);
            }).catch(err => console.log(err))
        },
        replayComments(blogId,parent){
            this.$emit('change:parent',{blogId,parent})
        }
    },
    watch:{
        randNum(){
            this.getComments();
            this.getCommentsCount();
        },
        nowPage(val){
            this.curPage = val;
            this.getComments();
        }
    }

}
</script>


<style lang="scss">
    @import "@/assets/css/about/leave-message.scss"
</style>